@import "mixins";

.testimonials {
  &__heading {
    @include crownHeadings;

  }
  &__person {
    margin-top: 35px;
  }
  @media screen and (min-width: 769px) {
    &__wrapper {
      margin-top: 70px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 0 85px;
    }
    &__person {
      margin: 0;
    }
  }
}
